nhtml {\n box-sizing: border-box;\n}\n*, *:before, *:after {\n box-sizing: inherit;\n}\n\nbody {\n  // display: flex;\n  // min-height: 100vh;\n  // flex-direction: column;\n}\n\nmain {\n  // flex: 1 0 auto;\n}\n\nbutton,\ninput,\noptgroup,\nselect,\ntextarea {\n  font-family: $font-stack;\n}\n\nul {\n  &:not(.browser-default) {\n    padding-left: 0;\n    list-style-type: none;\n\n    & > li {\n      list-style-type: none;\n    }\n  }\n}\n\na {\n\tcolor: $link-color;\n\ttext-decoration: none;\n\n  // Gets rid of tap active state\n  -webkit-tap-highlight-color: transparent;\n}\n\n\n// Positioning\n.valign-wrapper {\n  display: flex;\n  align-items: center;\n}\n\n\n// classic clearfix\n.clearfix {\n  clear: both;\n}\n\n\n// Z-levels\n.z-depth-0 {\n  box-shadow: none !important;\n}\n\n/* 2dp elevation modified*/\n.z-depth-1 {\n  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14),\n              0 3px 1px -2px rgba(0,0,0,0.12),\n              0 1px 5px 0 rgba(0,0,0,0.2);\n}\n.z-depth-1-half {\n  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.14), 0 1px 7px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -1px rgba(0, 0, 0, 0.2);\n}\n\n/* 6dp elevation modified*/\n.z-depth-2 {\n  box-shadow: 0 4px 5px 0 rgba(0,0,0,0.14),\n              0 1px 10px 0 rgba(0,0,0,0.12),\n              0 2px 4px -1px rgba(0,0,0,0.3);\n}\n\n/* 12dp elevation modified*/\n.z-depth-3 {\n  box-shadow: 0 8px 17px 2px rgba(0,0,0,0.14),\n              0 3px 14px 2px rgba(0,0,0,0.12),\n              0 5px 5px -3px rgba(0, 0, 0, 0.2);\n}\n\n/* 16dp elevation */\n.z-depth-4 {\n  box-shadow: 0 16px 24px 2px rgba(0,0,0,0.14),\n              0 6px 30px 5px rgba(0,0,0,0.12),\n              0 8px 10px -7px rgba(0,0,0,0.2);\n}\n\n/* 24dp elevation */\n.z-depth-5 {\n  box-shadow: 0 24px 38px 3px rgba(0,0,0,0.14),\n              0 9px 46px 8px rgba(0,0,0,0.12),\n              0 11px 15px -7px rgba(0,0,0,0.2);\n}\n\n.hoverable {\n  transition: box-shadow .25s;\n\n  &:hover {\n    box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);\n  }\n}\n\n// Dividers\n\n.divider {\n  height: 1px;\n  overflow: hidden;\n  background-color: color(\"grey\", \"lighten-2\");\n}\n\n\n//  Blockquote\n\nblockquote {\n  margin: 20px 0;\n  padding-left: 1.5rem;\n  border-left: 5px solid $primary-color;\n}\n\n// Icon Styles\n\ni {\n  line-height: inherit;\n\n  &.left {\n    float: left;\n    margin-right: 15px;\n  }\n  &.right {\n    float: right;\n    margin-left: 15px;\n  }\n  &.tiny {\n    font-size: 1rem;\n  }\n  &.small {\n    font-size: 2rem;\n  }\n  &.medium {\n    font-size: 4rem;\n  }\n  &.large {\n    font-size: 6rem;\n  }\n}\n\n// Images\nimg.responsive-img,\nvideo.responsive-video {\n  max-width: 100%;\n  height: auto;\n}\n\n\n// Pagination\n\n.pagination {\n\n  li {\n    display: inline-block;\n    border-radius: 2px;\n    text-align: center;\n    vertical-align: top;\n    height: 30px;\n\n    a {\n      color: #444;\n      display: inline-block;\n      font-size: 1.2rem;\n      padding: 0 10px;\n      line-height: 30px;\n    }\n\n    &.active a { color: #fff; }\n\n    &.active { background-color: $primary-color; }\n\n    &.disabled a {\n      cursor: default;\n      color: #999;\n    }\n\n    i {\n      font-size: 2rem;\n    }\n  }\n\n\n  li.pages ul li {\n    display: inline-block;\n    float: none;\n  }\n}\n@media #{$medium-and-down} {\n  .pagination {\n    width: 100%;\n\n    li.prev,\n    li.next {\n      width: 10%;\n    }\n\n    li.pages {\n      width: 80%;\n      overflow: hidden;\n      white-space: nowrap;\n    }\n  }\n}\n\n// Breadcrumbs\n.breadcrumb {\n  font-size: 18px;\n  color: rgba(255,255,255, .7);\n\n  i,\n  [class^=\"mdi-\"], [class*=\"mdi-\"],\n  i.material-icons {\n    display: inline-block;\n    float: left;\n    font-size: 24px;\n  }\n\n  &:before {\n    content: '\\E5CC';\n    color: rgba(255,255,255, .7);\n    vertical-align: top;\n    display: inline-block;\n    font-family: 'Material Icons';\n    font-weight: normal;\n    font-style: normal;\n    font-size: 25px;\