ll-and-down} {\n    display: block !important;\n  }\n}\n.show-on-medium-and-up {\n  @media #{$medium-and-up} {\n    display: block !important;\n  }\n}\n.show-on-medium-and-down {\n  @media #{$medium-and-down} {\n    display: block !important;\n  }\n}\n\n\n// Center text on mobile\n.center-on-small-only {\n  @media #{$small-and-down} {\n    text-align: center;\n  }\n}\n\n// Footer\n.page-footer {\n  padding-top: 20px;\n  color: $footer-font-color;\n  background-color: $footer-bg-color;\n\n  .footer-copyright {\n    overflow: hidden;\n    min-height: 50px;\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    padding: 10px 0px;\n    color: $footer-copyright-font-color;\n    background-color: $footer-copyright-bg-color;\n  }\n}\n\n// Tables\ntable, th, td {\n   border: none;\n}\n\ntable {\n  width:100%;\n  display: table;\n  border-collapse: collapse;\n  border-spacing: 0;\n\n  &.striped {\n    tr {\n      border-bottom: none;\n    }\n\n    > tbody {\n      > tr:nth-child(odd) {\n        background-color: $table-striped-color;\n      }\n\n      > tr > td {\n        border-radius: 0;\n      }\n    }\n  }\n\n  &.highlight > tbody > tr {\n    transition: background-color .25s ease;\n    &:hover {\n      background-color: $table-striped-color;\n    }\n  }\n\n  &.centered {\n    thead tr th, tbody tr td {\n      text-align: center;\n    }\n  }\n}\n\ntr {\n  border-bottom: 1px solid $table-border-color;\n}\n\ntd, th{\n  padding: 15px 5px;\n  display: table-cell;\n  text-align: left;\n  vertical-align: middle;\n  border-radius: 2px;\n}\n\n// Responsive Table\n@media #{$medium-and-down} {\n\n  table.responsive-table {\n    width: 100%;\n    border-collapse: collapse;\n    border-spacing: 0;\n    display: block;\n    position: relative;\n\n    td:empty:before {\n      content: '\\00a0';\n    }\n\n    th,\n    td {\n      margin: 0;\n      vertical-align: top;\n    }\n\n    th { text-align: left; }\n    thead {\n      display: block;\n      floa