 }\n\n\n    &:last-child {\n      border-bottom: none;\n    }\n\n    &.active {\n      background-color: $collection-active-bg-color;\n      color: $collection-active-color;\n\n      .secondary-content {\n        color: #fff;\n      }\n    }\n  }\n  a.collection-item{\n    display: block;\n    transition: .25s;\n    color: $collection-link-color;\n    &:not(.active) {\n      &:hover {\n        background-color: $collection-hover-bg-color;\n      }\n    }\n  }\n\n  &.with-header {\n    .collection-header {\n      background-color: $collection-bg-color;\n      border-bottom: 1px solid $collection-border-color;\n      padding: 10px 20px;\n    }\n    .collection-item {\n      padding-left: 30px;\n    }\n    .collection-item.avatar {\n      padding-left: 72px;\n    }\n  }\n\n}\n// Made less specific to allow easier overriding\n.secondary-content {\n  float: right;\n  color: $secondary-color;\n}\n.collapsible .collection {\n  margin: 0;\n  border: none;\n}\n\n\n\n// Responsive Videos\n.video-container {\n    position: relative;\n    padding-bottom: 56.25%;\n    height: 0;\n    overflow: hidden;\n\n    iframe, object, embed {\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 100%;\n    }\n}\n\n// Progress Bar\n.progress {\n    position: relative;\n    height: 4px;\n    display: block;\n    width: 100%;\n    background-color: lighten($progress-bar-color, 40%);\n    border-radius: 2px;\n    margin: $element-top-margin 0 $element-bottom-margin 0;\n    overflow: hidden;\n  .determinate {\n    position: absolute;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    background-color: $progress-bar-color;\n    transition: width .3s linear;\n  }\n  .indeterminate {\n    background-color: $progress-bar-color;\n    &:before {\n      content: '';\n      position: absolute;\n      background-color: inherit;\n      top: 0;\n      left:0;\n      bottom: 0;\n      will-change: left, right;\n      // Custom bezier\n      animation: indeterminate 2.1s 