abel,\n  .prefix ~ .helper-text,\n  .prefix ~ .autocomplete-content {\n    margin-left: 3rem;\n    width: 92%;\n    width: calc(100% - 3rem);\n  }\n\n  .prefix ~ label { margin-left: 3rem; }\n\n  @media #{$medium-and-down} {\n    .prefix ~ input {\n      width: 86%;\n      width: calc(100% - 3rem);\n    }\n  }\n\n  @media #{$small-and-down} {\n    .prefix ~ input {\n      width: 80%;\n      width: calc(100% - 3rem);\n    }\n  }\n}\n\n\n/* Search Field */\n\n.input-field input[type=search] {\n  display: block;\n  line-height: inherit;\n  transition: .3s background-color;\n\n  .nav-wrapper & {\n    height: inherit;\n    padding-left: 4rem;\n    width: calc(100% - 4rem);\n    border: 0;\n    box-shadow: none;\n  }\n\n  &:focus:not(.browser-default) {\n    background-color: $input-background;\n    border: 0;\n    box-shadow: none;\n    color: #444;\n\n    & + label i,\n    & ~ .mdi-navigation-close,\n    & ~ .material-icons {\n      color: #444;\n    }\n  }\n\n  & + .label-icon {\n    transform: none;\n    left: 1rem;\n  }\n\n  & ~ .mdi-navigation-close,\n  & ~ .material-icons {\n    position: absolute;\n    top: 0;\n    right: 1rem;\n    color: transparent;\n    cursor: pointer;\n    font-size: $input-icon-size;\n    transition: .3s color;\n  }\n}\n\n\n/* Textarea */\n\n// Default textarea\ntextarea {\n  width: 100%;\n  height: $input-height;\n  background-color: transparent;\n\n  &.materialize-textarea {\n    line-height: normal;\n    overflow-y: hidden; /* prevents scroll bar flash */\n    padding: .8rem 0 .8rem 0; /* prevents text jump on Enter keypress */\n    resize: none;\n    min-height: $input-height;\n    box-sizing: border-box;\n  }\n}\n\n// For textarea autoresize\n.hiddendiv {\n  visibility: hidden;\n  white-space: pre-wrap;\n  word-wrap: break-word;\n  overflow-wrap: break-word; /* future version of deprecated 'word-wrap' */\n  padding-top: 1.2rem; /* prevents text jump on Enter keypress */\n\n  // Reduces repaints\n  position: absolute;\n  top: 0;\n  z-index: -1;\n}\n\n\n/* Autocomplete */\n.autocomplete-content {\n  li {\n    .highlight { color: #444; }\n\n    img {\n      height: $dropdown-item-height - 10;\n      width: $dropdown-item-height - 10;\n      margin: 5px 15px;\n    }\n  }\n}\n\n/* Character Counter */\n.character-counter {\n  min-height: 18px;\n}\n","/* Radio Buttons\n   ========================================================================== */\n\n// Remove default Radio Buttons\n[type=\"radio\"]:not(:checked),\n[type=\"radio\"]:checked {\n  position: absolute;\n  opacity: 0;\n  pointer-events: none;\n}\n\n[type=\"radio\"]:not(:checked) + span,\n[type=\"radio\"]:checked + span {\n  position: relative;\n  padding-left: 35px;\n  cursor: pointer;\n  display: inline-block;\n  height: 25px;\n  line-height: 25px;\n  font-size: 1rem;\n  transition: .28s ease;\n  user-select: none;\n}\n\n[type=\"radio\"] + span:before,\n[type=\"radio\"] + span:after {\n  content: '';\n  position: absolute;\n  left: 0;\n  top: 0;\n  margin: 4px;\n  width: 16px;\n  height: 16px;\n  z-index: 0;\n  transition: .28s ease;\n}\n\n/* Unchecked styles */\n[type=\"radio\"]:not(:checked) + span:before,\n[type=\"radio\"]:not(:checked) + span:after,\n[type=\"radio\"]:checked + span:before,\n[type=\"radio\"]:checked + span:after,\n[type=\"radio\"].with-gap:checked + span:before,\n[type=\"radio\"].with-gap:checked + span:after {\n  border-radius: 50%;\n}\n\n[type=\"radio\"]:not(:checked) + span:before,\n[type=\"radio\"]:not(:checked) + span:after {\n  border: 2px solid $radio-empty-color;\n}\n\n[type=\"radio\"]:not(:checked) + span:after {\n  transform: scale(0);\n}\n\n/* Checked styles */\n[type=\"radio\"]:checked + span:before {\n  border: 2px solid transparent;\n}\n\n[type=\"radio\"]:checked + span:after,\n[type=\"radio\"].with-gap:checked + span:before,\n[type=\"radio\"].with-gap:checked + span:after {\n  border: $radio-border;\n}\n\n[type=\"radio\"]:checked + span:after,\n[type=\"radio\"].with-gap:checked + span:after {\n  background-color: $radio-fill-color;\n}\n\n[type=\"radio\"]:checked + span:after {\n  transform: scale(1.02);\n}\n\n/* Radio With gap */\n[type=\"radio\"].with-gap:checked + span:after {\n  transform: scale(.5);\n}\n\n/* Focused styles */\n[type=\"radio\"].tabbed:focus + span:before {\n  box-shadow: 0 0 0 10px rgba(0,0,0,.1);\n}\n\n/* Disabled Radio With gap */\n[type=\"radio\"].with-gap:disabled:checked + span:before {\n  border: 2px solid $input-disabled-color;\n}\n\n[type=\"radio\"].with-gap:disabled:checked + span:after {\n  border: none;\n  background-color: $input-disabled-color;\n}\n\n/* Disabled style */\n[type=\"radio\"]:disabled:not(:checked) + span:before,\n[type=\"radio\"]:disabled:checked + span:before {\n  background-color: transparent;\n  border-color: $input-disabled-color;\n}\n\n[type=\"radio\"]:disabled + span {\n  color: $input-disabled-color;\n}\n\n[type=\"radio\"]:disabled:not(:checked) + span:before {\n  border-color: $input-disabled-color;\n}\n\n[type=\"radio\"]:disabled:checked + span:after {\n  background-color: $input-disabled-color;\n  border-color: $input-disabled-solid-color;\n}\n","/* Checkboxes\n   ========================================================================== */\n\n/* Remove default checkbox */\n[type=\"checkbox\"]:not(:checked),\n[type=\"checkbox\"]:checked {\n  position: absolute;\n  opacity: 0;\n  pointer-events: none;\n}\n\n// Checkbox Styles\n[type=\"checkbox\"] {\n  // Text Label Style\n  + span:not(.lever) {\n    position: relative;\n    padding-left: 35px;\n    cursor: pointer;\n    display: inline-block;\n    height: 25px;\n    line-height: 25px;\n    font-size: 1rem;\n    user-select: none;\n  }\n\n  /* checkbox aspect */\n  + span:not(.lever):before,\n  &:not(.filled-in) + span:not(.lever):after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 18px;\n    height: 18px;\n    z-index: 0;\n    border: 2px solid $radio-empty-color;\n    border-radius: 1px;\n    margin-top: 3px;\n    transition: .2s;\n  }\n\n  &:not(.filled-in) + span:not(.lever):after {\n    border: 0;\n    transform: scale(0);\n  }\n\n  &:not(:checked):disabled + span:not(.lever):before {\n    border: none;\n    background-color: $input-disabled-color;\n  }\n\n  // Focused styles\n  &.tabbed:focus + span:not(.lever):after {\n    transform: scale(1);\n    border: 0;\n    border-radius: 50%;\n    box-shadow: 0 0 0 10px rgba(0,0,0,.1);\n    background-color: rgba(0,0,0,.1);\n  }\n}\n\n[type=\"checkbox\"]:checked {\n  + span:not(.lever):before {\n    top: -4px;\n    left: -5px;\n    width: 12px;\n    height: 22px;\n    border-top: 2px solid transparent;\n    border-left: 2px solid transparent;\n    border-right: $radio-border;\n    border-bottom: $radio-border;\n    transform: rotate(40deg);\n    backface-visibility: hidden;\n    transform-origin: 100% 100%;\n  }\n\n  &:disabled + span:before {\n    border-right: 2px solid $input-disabled-color;\n    border-bottom: 2px solid $input-disabled-color;\n  }\n}\n\n/* Indeterminate checkbox */\n[type=\"checkbox\"]:indeterminate {\n  + span:not(.lever):before {\n    top: -11px;\n    left: -12px;\n    width: 10px;\n    height: 22px;\n    border-top: none;\n    border-left: none;\n    border-right: $radio-border;\n    border-bottom: none;\n    transform: rotate(90deg);\n    backface-visibility: hidden;\n    transform-origin: 100% 100%;\n  }\n\n  // Disabled indeterminate\n  &:disabled + span:not(.lever):before {\n    border-right: 2px solid $input-disabled-color;\n    background-color: transparent;\n  }\n}\n\n// Filled in Style\n[type=\"checkbox\"].filled-in {\n  // General\n  + span:not(.lever):after {\n    border-radius: 2px;\n  }\n\n  + span:not(.lever):before,\n  + span:not(.lever):after {\n    content: '';\n    left: 0;\n    position: absolute;\n    /* .1s delay is for check animation */\n    transition: border .25s, background-color .25s, width .20s .1s, height .20s .1s, top .20s .1s, left .20s .1s;\n    z-index: 1;\n  }\n\n  // Unchecked style\n  &:not(:checked) + span:not(.lever):before {\n    width: 0;\n    height: 0;\n    border: 3px solid transparent;\n    left: 6px;\n    top: 10px;\n    transform: rotateZ(37deg);\n    transform-origin: 100% 100%;\n  }\n\n  &:not(:checked) + span:not(.lever):after {\n    height: 20px;\n    width: 20px;\n    background-color: transparent;\n    border: 2px solid $radio-empty-color;\n    top: 0px;\n    z-index: 0;\n  }\n\n  // Checked style\n  &:checked {\n    + span:not(.lever):before {\n      top: 0;\n      left: 1px;\n      width: 8px;\n      height: 13px;\n      border-top: 2px solid transparent;\n      border-left: 2px solid transparent;\n      border-right: 2px solid $input-background;\n      border-bottom: 2px solid $input-background;\n      transform: rotateZ(37deg);\n      transform-origin: 100% 100%;\n    }\n\n    + span:not(.lever):after {\n      top: 0;\n      width: 20px;\n      height: 20px;\n      border: 2px solid $secondary-color;\n      background-color: $secondary-color;\n      z-index: 0;\n    }\n  }\n\n  // Focused styles\n  &.tabbed:focus + span:not(.lever):after {\n    border-radius: 2px;\n    border-color: $radio-empty-color;\n    background-color: rgba(0,0,0,.1);\n  }\n\n  &.tabbed:checked:focus + span:not(.lever):after {\n    border-radius: 2px;\n    background-color: $secondary-color;\n    border-color: $secondary-color;\n  }\n\n  // Disabled style\n  &:disabled:not(:checked) + span:not(.lever):before {\n    background-color: transparent;\n    border: 2px solid transparent;\n  }\n\n  &:disabled:not(:checked) + span:not(.lever):after {\n    border-color: transparent;\n    background-color: $input-disabled-solid-color;\n  }\n\n  &:disabled:checked + span:not(.lever):before {\n    background-color: transparent;\n  }\n\n  &:disabled:checked + span:not(.lever):after {\n    background-color: $input-disabled-solid-color;\n    border-color: $input-disabled-solid-color;\n  }\n}\n","/* Switch\n   ========================================================================== */\n\n.switch,\n.switch * {\n  -webkit-tap-highlight-color: transparent;\n  user-select: none;\n}\n\n.switch label {\n  cursor: pointer;\n}\n\n.switch label input[type=checkbox] {\n  opacity: 0;\n  width: 0;\n  height: 0;\n\n  &:checked + .lever {\n    background-color: $switch-checked-lever-bg;\n\n    &:before, &:after {\n      left: 18px;\n    }\n\n    &:after {\n      background-color: $switch-bg-color;\n    }\n  }\n}\n\n.switch label .lever {\n  content: \"\";\n  display: inline-block;\n  position: relative;\n  width: 36px;\n  height: 14px;\n  background-color: $switch-unchecked-lever-bg;\n  border-radius: $switch-radius;\n  margin-right: 10px;\n  transition: background 0.3s ease;\n  vertical-align: middle;\n  margin: 0 16px;\n\n  &:before, &:after {\n    content: \"\";\n    position: absolute;\n    display: inline-block;\n    width: 20px;\n    height: 20px;\n    border-radius: 50%;\n    left: 0;\n    top: -3px;\n    transition: left 0.3s ease, background .3s ease, box-shadow 0.1s ease, transform .1s ease;\n  }\n\n  &:before {\n    background-color: transparentize($switch-bg-color, .85);\n  }\n\n  &:after {\n    background-color: $switch-unchecked-bg;\n    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);\n  }\n}\n\n// Switch active style\ninput[type=checkbox]:checked:not(:disabled) ~ .lever:active::before,\ninput[type=checkbox]:checked:not(:disabled).tabbed:focus ~ .lever::before {\n  transform: scale(2.4);\n  background-color: transparentize($switch-bg-color, .85);\n}\n\ninput[type=checkbox]:not(:disabled) ~ .lever:active:before,\ninput[type=checkbox]:not(:disabled).tabbed:focus ~ .lever::before {\n  transform: scale(2.4);\n  background-color: rgba(0,0,0,.08);\n}\n\n// Disabled Styles\n.switch input[type=checkbox][disabled] + .lever {\n  cursor: default;\n  background-color: rgba(0,0,0,.12);\n}\n\n.switch label input[type=checkbox][disabled] + .lever:after,\n.switch label input[type=checkbox][disabled]:checked + .lever:after {\n  background-color: $input-disabled-solid-color;\n}\n","/* Select Field\n   ========================================================================== */\n\nselect { display: none; }\nselect.browser-default { display: block; }\n\nselect {\n  background-color: $select-background;\n  width: 100%;\n  padding: $select-padding;\n  border: $select-border;\n  border-radius: $select-radius;\n  height: $input-height;\n}\n\n.select-label {\n  position: absolute;\n}\n\n.select-wrapper {\n  &.valid .helper-text[data-success],\n  &.invalid ~ .helper-text[data-error] {\n    @extend %hidden-text;\n  }\n\n  &.valid {\n    & > input.select-dropdown {\n      @extend %valid-input-style;\n    }\n\n    & ~ .helper-text:after {\n      @extend %custom-success-message;\n    }\n  }\n\n  &.invalid {\n    & > input.select-dropdown,\n    & > input.select-dropdown:focus {\n      @extend %invalid-input-style;\n    }\n\n    & ~ .helper-text:after {\n      @extend %custom-error-message;\n    }\n  }\n\n  &.valid + label,\n  &.invalid + label {\n    width: 100%;\n    pointer-events: none;\n  }\n\n  & + label:after {\n    @extend %input-after-style;\n  }\n\n  position: relative;\n\n  input.select-dropdown {\n    &:focus {\n      border-bottom: 1px solid $input-focus-color;\n    }\n    position: relative;\n    cursor: pointer;\n    background-color: transparent;\n    border: none;\n    border-bottom: $input-border;\n    outline: none;\n    height: $input-height;\n    line-height: $input-height;\n    width: 100%;\n    font-size: $input-font-size;\n    margin: $input-margin;\n    padding: 0;\n    display: block;\n    user-select:none;\n    z-index: 1;\n  }\n\n  .caret {\n    position: absolute;\n    right: 0;\n    top: 0;\n    bottom: 0;\n    margin: auto 0;\n    z-index: 0;\n    fill: rgba(0,0,0,.87);\n  }\n\n  & + label {\n    position: absolute;\n    top: -26px;\n    font-size: $label-font-size;\n  }\n}\n\n// Disabled styles\nselect:disabled {\n  color: $input-disabled-color;\n}\n\n.select-wrapper.disabled {\n  + label {\n    color: $input-disabled-color;\n  }\n  .caret {\n    fill: $input-disabled-color;\n  }\n}\n\n.select-wrapper input.select-dropdown:disabled {\n  color: $input-disabled-color;\n  cursor: default;\n  user-select: none;\n}\n\n.select-wrapper i {\n  color: $select-disabled-color;\n}\n\n.select-dropdown li.disabled,\n.select-dropdown li.disabled > span,\n.select-dropdown li.optgroup {\n  color: $select-disabled-color;\n  background-color: transparent;\n}\n\nbody.keyboard-focused {\n  .select-dropdown.dropdown-content li:focus {\n    background-color: $select-option-focus;\n  }\n}\n\n.select-dropdown.dropdown-content {\n  li {\n    &:hover {\n      background-color: $select-option-hover;\n    }\n\n    &.selected {\n      background-color: $select-option-selected;\n    }\n  }\n}\n\n// Prefix Icons\n.prefix ~ .select-wrapper {\n  margin-left: 3rem;\n  width: 92%;\n  width: calc(100% - 3rem);\n}\n\n.prefix ~ label { margin-left: 3rem; }\n\n// Icons\n.select-dropdown li {\n  img {\n    height: $dropdown-item-height - 10;\n    width: $dropdown-item-height - 10;\n    margin: 5px 15px;\n    float: right;\n  }\n}\n\n// Optgroup styles\n.select-dropdown li.optgroup {\n  border-top: 1px solid $dropdown-hover-bg-color;\n\n  &.selected > span {\n    color: rgba(0, 0, 0, .7);\n  }\n\n  & > span {\n    color: rgba(0, 0, 0, .4);\n  }\n\n  & ~ li.optgroup-option {\n    padding-left: 1rem;\n  }\n}\n","/* File Input\n   ========================================================================== */\n\n.file-field {\n  position: relative;\n\n  .file-path-wrapper {\n    overflow: hidden;\n    padding-left: 10px;\n  }\n\n  input.file-path { width: 100%; }\n\n  .btn {\n    float: left;\n    height: $input-height;\n    line-height: $input-height;\n  }\n\n  span {\n    cursor: pointer;\n  }\n\n  input[type=file] {\n\n    // Needed to override webkit button\n    &::-webkit-file-upload-button {\n      display: none;\n    }\n\n    position: absolute;\n    top: 0;\n    right: 0;\n    left: 0;\n    bottom: 0;\n    width: 100%;\n    margin: 0;\n    padding: 0;\n    font-size: 20px;\n    cursor: pointer;\n    opacity: 0;\n    filter: alpha(opacity=0);\n  }\n}\n","/* Range\n   ========================================================================== */\n\n.range-field {\n  position: relative;\n}\n\ninput[type=range],\ninput[type=range] + .thumb {\n  @extend .no-select;\n  cursor: pointer;\n}\n\ninput[type=range] {\n  position: relative;\n  background-color: transparent;\n  border: none;\n  outline: none;\n  width: 100%;\n  margin: 15px 0;\n  padding: 0;\n\n  &:focus {\n    outline: none;\n  }\n}\n\ninput[type=range] + .thumb {\n  position: absolute;\n  top: 10px;\n  left: 0;\n  border: none;\n  height: 0;\n  width: 0;\n  border-radius: 50%;\n  background-color: $radio-fill-color;\n  margin-left: 7px;\n\n  transform-origin: 50% 50%;\n  transform: rotate(-45deg);\n\n  .value {\n    display: block;\n    width: 30px;\n    text-align: center;\n    color: $radio-fill-color;\n    font-size: 0;\n    transform: rotate(45deg);\n  }\n\n  &.active {\n    border-radius: 50% 50% 50% 0;\n\n    .value {\n      color: $input-background;\n      margin-left: -1px;\n      margin-top: 8px;\n      font-size: 10px;\n    }\n  }\n}\n\n// Shared\n@mixin range-track {\n  height: $track-height;\n  background: #c2c0c2;\n  border: none;\n}\n\n@mixin range-thumb {\n  border: none;\n  height: $range-height;\n  width: $range-width;\n  border-radius: 50%;\n  background: $radio-fill-color;\n  transition: box-shadow .3s;\n}\n\n// WebKit\ninput[type=range] {\n  -webkit-appearance: none;\n}\n\ninput[type=range]::-webkit-slider-runnable-track {\n  @include range-track;\n}\n\ninput[type=range]::-webkit-slider-thumb {\n  @include range-thumb;\n  -webkit-appearance: none;\n  background-color: $radio-fill-color;\n  transform-origin: 50% 50%;\n  margin: -5px 0 0 0;\n\n}\n\n.keyboard-focused input[type=range]:focus:not(.active)::-webkit-slider-thumb {\n  box-shadow: 0 0 0 10px rgba($radio-fill-color, .26);\n}\n\n// FireFox\ninput[type=range] {\n  /* fix for FF unable to apply focus style bug  */\n  border: 1px solid white;\n\n  /*required for proper track sizing in FF*/\n}\n\ninput[type=range]::-moz-range-track {\n  @include range-track;\n}\n\ninput[type=range]::-moz-focus-inner {\n  border: 0;\n}\n\ninput[type=range]::-moz-range-thumb {\n  @include range-thumb;\n  margin-top: -5px;\n}\n\n// hide the outline behind the border\ninput[type=range]:-moz-focusring {\n  outline: 1px solid #fff;\n  outline-offset: -1px;\n}\n\n.keyboard-focused input[type=range]:focus:not(.active)::-moz-range-thumb {\n  box-shadow: 0 0 0 10px rgba($radio-fill-color, .26);\n}\n\n// IE 10+\ninput[type=range]::-ms-track {\n  height: $track-height;\n\n  // remove bg colour from the track, we'll use ms-fill-lower and ms-fill-upper instead\n  background: transparent;\n\n  // leave room for the larger thumb to overflow with a transparent border */\n  border-color: transparent;\n  border-width: 6px 0;\n\n  /*remove default tick marks*/\n  color: transparent;\n}\n\ninput[type=range]::-ms-fill-lower {\n  background: #777;\n}\n\ninput[type=range]::-ms-fill-upper {\n  background: #ddd;\n}\n\ninput[type=range]::-ms-thumb {\n  @include range-thumb;\n}\n\n.keyboard-focused input[type=range]:focus:not(.active)::-ms-thumb {\n  box-shadow: 0 0 0 10px rgba($radio-fill-color, .26);\n}\n","/*\n    @license\n    Copyright (c) 2014 The Polymer Project Authors. All rights reserved.\n    This code may only be used under the BSD style license found at http://polymer.github.io/LICENSE.txt\n    The complete set of authors may be found at http://polymer.github.io/AUTHORS.txt\n    The complete set of contributors may be found at http://polymer.github.io/CONTRIBUTORS.txt\n    Code distributed by Google as part of the polymer project is also\n    subject to an additional IP rights grant found at http://polymer.github.io/PATENTS.txt\n */\n\n/**************************/\n/* STYLES FOR THE SPINNER */\n/**************************/\n\n/*\n * Constants:\n *      STROKEWIDTH = 3px\n *      ARCSIZE     = 270 degrees (amount of circle the arc takes up)\n *      ARCTIME     = 1333ms (time it takes to expand and contract arc)\n *      ARCSTARTROT = 216 degrees (how much the start location of the arc\n *                                should rotate each time, 216 gives us a\n *                                5 pointed star shape (it's 360/5 * 3).\n *                                For a 7 pointed star, we might do\n *                                360/7 * 3 = 154.286)\n *      CONTAINERWIDTH = 28px\n *      SHRINK_TIME = 400ms\n */\n\n\n.preloader-wrapper {\n  display: inline-block;\n  position: relative;\n  width: 50px;\n  height: 50px;\n\n  &.small {\n    width: 36px;\n    height: 36px;\n  }\n\n  &.big {\n    width: 64px;\n    height: 64px;\n  }\n\n  &.active {\n    /* duration: 360 * ARCTIME / (ARCSTARTROT + (360-ARCSIZE)) */\n    -webkit-animation: container-rotate 1568ms linear infinite;\n    animation: container-rotate 1568ms linear infinite;\n  }\n}\n\n@-webkit-keyframes container-rotate {\n  to { -webkit-transform: rotate(360deg) }\n}\n\n@keyframes container-rotate {\n  to { transform: rotate(360deg) }\n}\n\n.spinner-layer {\n  position: absolute;\n  width: 100%;\n  height: 100%;\n  opacity: 0;\n  border-color: $spinner-default-color;\n}\n\n.spinner-blue,\n.spinner-blue-only {\n  border-color: #4285f4;\n}\n\n.spinner-red,\n.spinner-red-only {\n  border-color: #db4437;\n}\n\n.spinner-yellow,\n.spinner-yellow-only {\n  border-color: #f4b400;\n}\n\n.spinner-green,\n.spinner-green-only {\n  border-color: #0f9d58;\n}\n\n/**\n * IMPORTANT NOTE ABOUT CSS ANIMATION PROPERTIES (keanulee):\n *\n * iOS Safari (tested on iOS 8.1) does not handle animation-delay very well - it doesn't\n * guarantee that the animation will start _exactly_ after that value. So we avoid using\n * animation-delay and instead set custom k